      </div>
      <div class="stat-card">
        <h3>Available for Payment</h3>
        <div class="stat-value">{{ stats.totalAvailable.toFixed(4) }} SOL</div>
      </div>
    </div>
    
    <!-- Список рефералов -->
    <div class="referrals-list">
      <h2>Your Referrals</h2>
      
      <div v-if="loading" class="loading">
        <p>Loading referrals...</p>
      </div>
      
      <div v-else-if="referrals.length === 0" class="no-referrals">
        <p>You don't have any referrals yet. Share your referral link to start earning rewards!</p>
      </div>
      
      <div v-else class="referral-items">
        <div v-for="referral in referrals" :key="referral.id" class="referral-item">
          <div class="referral-info">
            <div class="referral-details">
              <div class="referral-tag">{{ referral.referralTag }}</div>
              <div class="referral-date">Last action: {{ formatDate(referral.lastAction) }}</div>
              <div class="referral-amount">Available: {{ referral.availableForPayment.toFixed(4) }} SOL</div>
              <div class="referral-join-date">Joined: {{ formatDate(referral.createdAt) }}</div>
            </div>
          </div>
          <div class="referral-actions">
            <button 
              v-if="referral.canReceivePayment" 
              class="receive-button" 
              @click="receivePayment(referral.id)"
              :disabled="processingPayment === referral.id"
            >
              {{ processingPayment === referral.id ? 'Processing...' : 'Receive Payment' }}
            </button>
            <div v-else class="payment-info">
              <span v-if="referral.availableForPayment <= 0.1">
                Minimum 0.1 SOL required
              </span>
              <span v-else>
                14 days since last action required
              </span>
