            <div class="tier-header">Silver</div>
            <div class="tier-percentage">3%</div>
            <div class="tier-requirement">10,000 BITCAT</div>
          </div>
          <div class="tier" :class="{ 'active': bitcatBalance >= 50001 && bitcatBalance <= 100000 }">
            <div class="tier-header">Gold</div>
            <div class="tier-percentage">4%</div>
            <div class="tier-requirement">50,000 BITCAT</div>
          </div>
          <div class="tier" :class="{ 'active': bitcatBalance >= 100001 }">
