            <span class="action-description">When your referrals participate in Memepad token launches</span>
          </li>
          <li>
            <span class="action-name">More Coming Soon</span>
            <span class="action-description">Additional paid actions will be added in the future</span>
          </li>
        </ul>
        
        <div class="rewards-note">
          <p><strong>Note:</strong> To increase your reward percentage, you need to hold more BITCAT tokens in your connected wallet.</p>
