<script>
import { ref, onMounted, computed, watch } from 'vue';
import store from '@/vuex';
import axios from 'axios';

export default {
  name: 'UserReferralsPage',
  
  setup() {
    const wallet = computed(() => store.getters.walletAddress);
    const userTag = computed(() => store.getters.userTag);
    const isAuthenticated = computed(() => store.getters.isAuthenticated);
    
    const referrals = ref([]);
    const stats = ref({
      totalReferrals: 0,
      totalAvailable: 0
    });
    const loading = ref(true);
    const processingPayment = ref(null);
    const referralLinkInput = ref(null);
    const referralLinkCopied = ref(false);
    const bitcatBalance = ref(0);
    const showRewardsPopup = ref(false);
    
    // Вычисляем процент вознаграждения на основе баланса BITCAT
    const rewardPercentage = computed(() => {
      if (bitcatBalance.value >= 100001) return 5;
      if (bitcatBalance.value >= 50001) return 4;
      if (bitcatBalance.value >= 10001) return 3;
      if (bitcatBalance.value >= 1000) return 2;
      return 1;
    });
    
    // Получение баланса BITCAT
    const getBitcatBalance = async () => {
      if (!wallet.value || !wallet.value) return;
      
      try {
        const response = await axios.get(`/dapp/get-bitcat-balance/${wallet.value}`);
        if (response.data && response.data.balance !== undefined) {
          bitcatBalance.value = response.data.balance;
        }
      } catch (error) {
        console.error('Error fetching BITCAT balance:', error);
      }
    };

    const formatStakedAmount = computed(() => {
      return (value) => {
        if (!value) return '0';
        return value.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',');
      };
    });
    
    // Отслеживаем изменение подключения кошелька
    watch(() => wallet.value, (newValue) => {
      if (newValue) {
        getBitcatBalance();
      } else {
        bitcatBalance.value = 0;
      }
    });
    
    const toast = ref({
      show: false,
      message: '',
      type: 'success'
    });
    
    // Загрузка данных о рефералах
    const loadReferrals = async () => {
      if (!isAuthenticated.value) return;
      
      try {
        loading.value = true;
        
        // Загрузка списка рефералов
        const response = await axios.get(`/referrals/${userTag.value}`);
        if (response.data.success) {
          referrals.value = response.data.referrals;
        }
        
        // Загрузка статистики
        const statsResponse = await axios.get(`/referrals/${userTag.value}/stats`);
        if (statsResponse.data.success) {
          stats.value = statsResponse.data.stats;
        }
      } catch (error) {
        console.error('Error loading referrals:', error);
        showToast('Failed to load referrals', 'error');
      } finally {
        loading.value = false;
      }
    };
    
    // Получение выплаты
    const receivePayment = async (referralId) => {
      if (processingPayment.value) return;
      
      try {
        processingPayment.value = referralId;
        
        const response = await axios.post(`/referrals/${userTag.value}/receive-payment`, {
          referralId
        });
        
        if (response.data.success) {
          showToast(`Payment of ${response.data.amount} SOL received successfully`, 'success');
          
          // Обновляем данные после успешной выплаты
          await loadReferrals();
        } else {
          showToast(response.data.message || 'Failed to receive payment', 'error');
        }
      } catch (error) {
        console.error('Error receiving payment:', error);
        showToast('Failed to receive payment', 'error');
      } finally {
        processingPayment.value = null;
      }
    };
    
    // Форматирование даты
    const formatDate = (dateString) => {
      if (!dateString) return 'Never';
      const date = new Date(dateString);
      return date.toLocaleDateString('en-US', {
        year: 'numeric',
        month: 'long',
        day: 'numeric',
        hour: '2-digit',
        minute: '2-digit'
      });
    };
    
    // Показ уведомления
    const showToast = (message, type = 'success') => {
      toast.value = {
        show: true,
        message,
        type
      };
      
      setTimeout(() => {
        toast.value.show = false;
      }, 3000);
    };
    
    // Метод для копирования реферальной ссылки
    const copyReferralLink = async () => {
      try {
        const referralUrl = `https://solanabitcat.com/?r=${userTag.value}`;
        await navigator.clipboard.writeText(referralUrl);
        
        // Показываем текст "Copied" на кнопке
        referralLinkCopied.value = true;
        
        // Через 2 секунды возвращаем исходный текст
        setTimeout(() => {
          referralLinkCopied.value = false;
        }, 2000);
        
        // Показываем уведомление
        showToast('Referral link copied to clipboard');
      } catch (error) {
        console.error('Failed to copy text: ', error);
        showToast('Failed to copy referral link', 'error');
      }
    };
    
    onMounted(() => {
      if (isAuthenticated.value) {
        loadReferrals();
      }
      
      // Загрузка баланса BITCAT, если кошелек подключен
      if (wallet.value) {
        getBitcatBalance();
      }
    });
    
    return {
      userTag,
      referrals,
      stats,
      loading,
      processingPayment,
      toast,
      referralLinkInput,
      referralLinkCopied,
      formatDate,
      receivePayment,
      copyReferralLink,
      isAuthenticated,
      bitcatBalance,
      rewardPercentage,
      showRewardsPopup,
      formatStakedAmount
    };
  }
};
</script>

